@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
:root {
  --primary-color: #e368f3;
  --background-color: #242424;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  font-size: 16px;
}

body {
  font-family: "Roboto", sans-serif;
  line-height: 1.5;
  background-color: #242424;
  background-color: var(--background-color);
  color: white;
  text-transform: uppercase;
}

a {
  text-decoration: none;
  color: white;
}

h1 {
  font-size: 3.75em;
  line-height: 1.2;
  margin-bottom: 15px;
}

.alias {
  color: #e368f3;
  color: var(--primary-color);
  font-weight: 700;
  font-size: inherit;
}

.title-line {
  width: 50%;
  height: 4px;
  margin: auto;
  border-width: 0;
  background-color: #e368f3;
  background-color: var(--primary-color);
  transition: 0.8s;
}

.section-divider {
  width: 70%;
  height: 2px;
  margin: 1.5em auto;
  border-width: 0;
  background-color: #fff;
  transition: 0.8s;
}

@media only screen and (max-width: 960px) {
  * {
    font-size: 14px;
  }
  .experiences {
    width: 90% !important;
    transition: 0.8s;
  }

  .skills {
    width: 90% !important;
  }

  .navbar {
    justify-content: center !important;
  }

  .desc {
    width: 90% !important;
  }

  .jobName hr {
    width: 60% !important;
  }

  .project {
    width: 95% !important;
  }

  .project-name hr {
    width: 50% !important;
  }
  .project-img {
    display: none;
  }
}

.navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  text-transform: uppercase;
  z-index: 20;
  padding: 0.3em 1em 0.5em;
  font-size: 2em;
  font-weight: 700;
  background-color: rgba(36, 36, 36, 0.7);
}

.navbar a {
  font-size: inherit;
}

.main {
  margin: 0 20px;
}

.show-links {
  display: flex;
  flex-direction: row;
  align-items: center;
  list-style-type: none;
  font-size: inherit;
}

.link {
  padding: 0 20px;
  transition: 0.8s;
  font-size: inherit;
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}

.line {
  width: 50%;
  height: 4px;
  margin: auto;
  border-width: 0;
  background-color: white;
  transition: 0.8s;
}

.link:hover .line {
  width: 100%;
}

.active .line {
  background-color: var(--primary-color);
  width: 100%;
}

.hamburger {
  font-size: inherit;
  cursor: pointer;
  padding: 0.5em;
}

.showcase {
  position: relative;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 0 20px;
  color: white;
  font-size: 16px;
}

.video-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: var(--background-color) no-repeat center center/cover;
}

.video-container:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* background-image: linear-gradient(
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0),
    var(--background-color)
  ); */
}

.video-container video {
  min-width: 100%;
  min-height: 100%;
  object-fit: cover;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.content {
  z-index: 10;
  /* background-image: linear-gradient(#000, rgba(0, 0, 0, 0)); */
  /* background-color: rgba(36, 36, 36, 0.6); */
  padding: 10px 10px;
  text-transform: uppercase;
  font-weight: 700;
  font-size: inherit;
}

.content h1 {
  background-color: rgba(36, 36, 36, 0.7);
}

.content h3 {
  background-color: rgba(36, 36, 36, 0.7);
  font-size: inherit;
}

.btn {
  display: inline-block;
  padding: 10px 30px;
  color: #fff;
  margin-top: 0.3em;
  font-size: 5em;
  transition: 0.8s;
  text-shadow: 0 5px var(--primary-color);
}

.btn i {
  font-size: inherit;
}

.btn:hover {
  transform: translate(0, 0.3em);
  text-shadow: 0 10px var(--primary-color);
}

.about {
  display: flex;
}

.title {
  font-size: 1.5em;
  transition: 0.8s;
  display: flex;
  justify-content: center;
}

.title h2 {
  font-size: 1.5em;
}

.about-card {
  display: flex;
  justify-content: center;
  padding: 2em 0 1em;
}

.name {
  font-size: 2em;
}

.profile {
  height: 12em;
  border: 0.3em solid #fff;
  border-radius: 50%;
}

.subsection {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 0 0 0 2em;
}

.subtitle {
  text-align: start;
  font-size: 1.3em;
  padding: 0.6em 0 0.3em;
}

.subtitle hr {
  width: 30%;
  margin: 0;
}

.desc {
  width: 55%;
  margin: auto;
  text-transform: none;
  text-transform: initial;
  color: #ddd;
}

.skills-container .subtitle {
  padding-top: 1em;
  text-align: center;
}

.skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 60%;
  margin: auto;
}

.skill {
  list-style-type: none;
  padding: 0 1em;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.skill i {
  color: var(--primary-color);
  margin-left: 0.5em;
}

.experiences {
  width: 60%;
  margin: auto;
  padding: 2em 0;
}
.job {
  margin-top: 1em;
}

.jobTop {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.time {
  padding: 0 0 0 1em;
  text-align: end;
}

.jobName {
  text-align: start;
  font-size: 1.3em;
  padding: 0.6em 0 0.3em;
  display: inline-block;
}

.jobName hr {
  width: 80%;
  height: 4px;
  background-color: var(--primary-color);
  border-width: 0;
}

.jobTitle {
  padding: 0.5em 0;
  font-style: italic;
}

.duty {
  padding: 0.5em 0;
  list-style: none;
  text-transform: none;
  text-transform: initial;
  color: #ddd;
}

.duty::before {
  content: "◆"; /* Insert content that looks like bullets */
  padding-right: 8px;
  color: var(--primary-color); /* Or a color you prefer */
}

.projects-container {
  margin: 3em 0;
}

.project {
  display: flex;
  flex-direction: row;
  background-color: rgba(0, 0, 0, 0.3);
  margin: 1em auto;
  width: 80%;
  align-items: center;
  justify-content: space-between;
  min-height: 10em;
}

.project-img {
  width: 40%;
  object-fit: cover;
  height: 100%;
}

.project-about {
  width: 100%;
  padding: 0 1em;
  margin: 1em 0;
}

.project-name {
  font-size: 1.5em;
  margin-bottom: 1em;
  display: inline-block;
}

.project-name hr {
  width: 80%;
  height: 4px;
  background-color: var(--primary-color);
  border-width: 0;
}

.project-desc {
  flex-wrap: wrap;
  margin-bottom: 1em;
  text-transform: none;
  text-transform: initial;
  color: #ddd;
}

.project-techs {
  color: var(--primary-color);
  display: flex;
  flex-direction: row;
  list-style-type: none;
  flex-wrap: wrap;
}

.tech {
  margin-right: 1em;
}

.project-links {
  display: flex;
  flex-direction: column;
  margin-right: 1em;
}

.project-link {
  display: flex;
  padding: 0.4em;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background-color: var(--primary-color);
  font-weight: 700;
  margin: 0.5em 0;
  border-radius: 10px;
  flex-wrap: nowrap;
  transition: 0.5s;
}

.project-link i {
  margin-right: 0.5em;
}

.project-link:hover {
  color: var(--primary-color);
  background-color: #fff;
}

.contact-container {
  padding: 3em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.socials {
  display: flex;
  flex-direction: row;
}

.social {
  margin: 0.5em;
  padding: 0.5em;
  background-color: var(--primary-color);
  text-align: center;
  border-radius: 10px;
  transition: 0.5s;
}

.social:hover {
  color: var(--primary-color);
  background-color: #fff;
}

.email {
  text-transform: none;
  text-transform: initial;
}

.contacts {
  margin-top: 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact {
  margin: 0.4em 0;
}

